/*
  Date Input Styles (for filter date pickers)
  Standardized for all filter UIs
*/
.filter-date-input {
  height: 2.5rem;
  border: 1px solid var(--border);
  border-radius: var(--radius-md);
  padding: 0 1rem;
  font-size: 1rem;
  background: var(--bg);
  color: var(--text-primary);
  outline: none;
  transition: border-color 0.2s;
  min-width: 140px;
}
.filter-date-input:focus {
  border-color: var(--primary);
}
/* Remove default calendar icon background in Chrome */
.filter-date-input::-webkit-input-placeholder { color: var(--text-secondary); }
.filter-date-input::-webkit-calendar-picker-indicator {
  filter: invert(0.3);
  opacity: 0.7;
  cursor: pointer;
}
.filter-date-input:disabled {
  background: var(--bg-hover);
  color: var(--text-disabled);
  cursor: not-allowed;
}
/*
  Filter Button Styles (for filter chips like Completed/Cancelled)
  Standardized for all filter UIs
*/
.filter-button {
  border: 1px solid var(--border);
  background: var(--bg);
  color: var(--text-primary);
  padding: 0.5rem 0.75rem;
  border-radius: var(--radius-md);
  cursor: pointer;
  font-weight: 700;
  font-size: 0.95rem;
  transition: border-color 120ms ease, background 120ms ease;
  height: 2.125rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.filter-button.active {
  border-color: var(--primary);
  background: color-mix(in srgb, var(--primary) 12%, transparent);
  color: var(--text-primary);
}

.filter-button:hover,
.filter-button:focus-visible {
  border-color: var(--primary);
}
/*
  Price Range Inputs (min/max price)
  Standardized for all filter UIs
*/
.filter-input-group {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.filter-label {
  font-size: 0.95rem;
  color: var(--text-secondary);
  margin-bottom: 0.25rem;
  font-weight: 500;
}

.filter-price-range {
  display: flex;
  gap: 0.5rem;
}

.filter-price-input {
  height: 2.5rem;
  border: 1px solid var(--border);
  border-radius: var(--radius-md);
  padding: 0 1rem;
  font-size: 1rem;
  background: var(--bg-surface);
  color: var(--text-primary);
  outline: none;
  transition: border-color 0.2s;
  width: 7rem;
  /* Remove number spinners for all browsers */
  -moz-appearance: textfield;
}
.filter-price-input:focus {
  border-color: var(--primary);
}
/* Remove number spinners Chrome, Safari, Edge */
.filter-price-input::-webkit-outer-spin-button,
.filter-price-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/*
  filters_styling.css
  --------------------------------------------------
  SINGLE SOURCE OF TRUTH for all filter/search UI styling.
  Only edit this file to update filter bar, search input, and tab styles.
  Used by admin/vendor filter UIs. Do NOT duplicate these styles elsewhere.
*/

.filter-bar {
  display: flex;
  align-items: center;
  gap: 1rem;
  background: var(--color-bg-secondary);
  padding: 0.75rem 1rem;
  border-radius: var(--radius-md);
  box-shadow: var(--shadow-xs);
}

.filter-input {
  height: 2.5rem;
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  padding: 0 1rem;
  font-size: 1rem;
  background: var(--color-bg);
  color: var(--color-text);
  outline: none;
  transition: border-color 0.2s;
}

.filter-input:focus {
  border-color: var(--color-primary);
}

.filter-tabs {
  display: flex;
  gap: 0.5rem;
}

.filter-tab {
  height: 2.5rem;
  padding: 0 1.25rem;
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  background: var(--color-bg);
  color: var(--color-text);
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s, color 0.2s, border-color 0.2s;
}

.filter-tab.active {
  background: var(--color-primary);
  color: var(--color-bg);
  border-color: var(--color-primary);
}
